import React from 'react';
import './Navbar.css';
import { Link } from 'react-router-dom';

const Navbar = () => {
    return (
        <div className="container">
            <div className="row">
            <div className="col md-4">
                <h1>Winter Perfumes</h1>
            </div>
            <div className="col md-6">
                <Link to ="/"
                style={{textDecoration:'none'}}
                className="home">
                    <span>Home </span>
                </Link>
                <Link to ="/products/buly"
                style={{textDecoration:'none'}}
                className="navbuly">
                    <span>BULY </span>
                </Link>
                <Link to ="/products/santamaria"
                style={{textDecoration:'none'}}
                className="navsanta">
                    <span>Santa Maria </span>
                </Link>
                <Link to ="/products/jomalone"
                style={{textDecoration:'none'}}
                className="navjo">
                    <span>Jo MALONE </span>
                </Link>
            </div>
            </div>
        </div>
    );

};

export default Navbar;