import React from 'react';
import santa from '../img/santa.jpg';
import './Santa.css'

const Santa = (props) => {
    return (
        <div className="santa">
            <img src={santa} />
                <div className="santa-desc">
                    <span>산타마리아노벨라 엔젤 디 피렌체</span><br/>
                    <span>정상가 : {props.perfumesdetail[1].price} </span><br/>
                    <span>회원가 : 160000</span><br/>
                    <button>바로 구매</button>
                </div>
           
        </div>
    );
};

export default Santa;