import React from 'react';
import jo from '../img/jomalone.jpg';
import './Jo.css';

const Jo = (props) => {
    return (
        <div className="jo">
        <img src={jo} />
            <div className="jo-desc">
                <span>조말론 우드 세이지 앤 시 솔트</span><br/>
                <span>정상가 : {props.perfumesdetail[2].price} </span><br/>
                <span>회원가 : 160000</span><br/>
                <button>바로 구매</button>
            </div>
       
    </div>
    );
};

export default Jo;