import React from 'react'
import './Card.css';

const Card = (props) => {

    return (
        <div className="col md-4">
            <div className="main">
                <img src={ require('../img/perfume' + props.i + '.jpg').default } alt=""/>
                <div className="detail">
                    <strong>{props.perfumes.title}</strong><br/>
                    <span>{props.perfumes.content}</span><br/>
                    <span>{props.perfumes.price}</span>
                </div>
            </div>
        </div>
    );
}

export default Card;