import React, { useState } from 'react';
import { Carousel } from 'react-bootstrap';
import Navbar from './components/Navbar';
import Card from './components/Card';
import Data from './data';
import Buly from './components/Buly'
import Santa from './components/Santa';
import Jo from './components/Jo';
import { Link, Route, Switch } from 'react-router-dom';

import './App.css';

function App() {
  let [perfumes, setPerfumes] = useState(Data);

  return (
    <div className="App">
      <Navbar />
      
      <Switch>
        <Route exact path="/">
          <Carousel>
            <Carousel.Item>
              <img
                className="d-block w-100 carousel-first"
                alt=""
              />
              <Carousel.Caption>
                <h3>Perfume makes you special</h3>
                <p>Find Your Own Perfume</p>
              </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item>
              <img
                className="d-block w-100 carousel-second"
                alt=""
              />
              <Carousel.Caption>
                <h3 style={{color:'black'}}>30% Season off</h3>
                <p style={{color:'black'}}>Your own perfume for special moments</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
              <img
                className="d-block w-100 carousel-third"
                alt=""
              />

              <Carousel.Caption>
                <h3>Christmas season special</h3>
                <p>Discover the Christmas season perfume</p>
              </Carousel.Caption>
            </Carousel.Item>
          </Carousel>

          <div className="container">
            <div className="row">
              {
                perfumes.map((a, i)=>{
                  return <Card perfumes={perfumes[i]} i={i} />
                })
              }
            </div>
          </div>
        </Route>
        <Route path="/products/buly">
          <Buly perfumesdetail={perfumes}/>
        </Route>
        <Route path="/products/santamaria">
          <Santa perfumesdetail={perfumes}/>
        </Route>
        <Route path="/products/jomalone">
          <Jo perfumesdetail={perfumes}/>
        </Route>
      </Switch>

      {/* <Card perfumes={perfumes}/> */}
    </div>
  );
}

export default App;
