import React, { useState, useEffect } from 'react';
import buly from '../img/buly.jpg';
import './Buly.css';

const Buly = (props) => {

    let [alert, setAlert] = useState(true);

    useEffect(()=>{
        let timer = setTimeout( ()=> setAlert(false) , 8000)
        return ()=>{clearTimeout(timer)}
    }, []);

    return (
        <div>

        <div className="buly">
            <div className="buly-img">
                <img src={buly} />
            </div>

            <div className="buly-desc">
                
            {
            alert === true
            ?
                <p>Best! 품절 임박</p>
            :null
            }
                <div className="buly-detail">
                    <span>불리1803 목욕하는 여인</span><br/>
                    <span>정상가 : {props.perfumesdetail[0].price}</span><br/>
                    <span>회원가 : 200000</span><br/>
                    <button>구매하기</button>
                </div>
            </div>
        </div>
    </div>
    );
};

export default Buly;